@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
*{padding:0;margin:0;box-sizing:border-box}body{background-color:#22254b;color:white;font-family:"Poppins", sans-serif;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin:0}header{background-color:#373b69;padding:1rem;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end}.search{background-color:transparent;border:2px solid #22254b;border-radius:50px;color:white;font-family:inherit;font-size:1.2rem;padding:0.5rem 1.5rem}.search:focus{outline:none}.movie-container{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center}.movie{background-color:#373b69;border-radius:3px;box-shadow:3px 3px 5px rgba(0,0,0,0.1);overflow:hidden;position:relative;margin:1rem;width:250px}.movie-info{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;padding:1rem}.movie-info h3{margin:0}.movie img{object-fit:cover;height:450px;width:100%}.movie-over{background-color:#fff;border-top-left-radius:3px;border-top-right-radius:3px;box-shadow:3px 3px 5px rgba(0,0,0,0.1);color:#22254b;padding:1rem;position:absolute;bottom:0;left:0;right:0;overflow:auto;max-height:100%;-webkit-transform:translateY(100%);transform:translateY(100%);transition:-webkit-transform 0.3s ease-in-out;transition:transform 0.3s ease-in-out;transition:transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out}.movie:hover .movie-over{-webkit-transform:translateY(0%);transform:translateY(0%)}.tag{background-color:#22254b;border-radius:5px;padding:0.5rem;font-weight:bold}.tag.green{color:green}.tag.orange{color:orange}.tag.red{color:red}

